/*antd override*/
.input{
    border-radius: 2px !important;
    border: 1px solid gray !important;
    height: 40px !important;
}
.ant-form-item{
    margin: 10px !important;
}
.primarybutton{
    background-color: #053323 !important;
    border: #2462e8 !important;
    height: 40px !important;
    width: 100% !important;
    color: white !important;
    font-size: 16px !important;
    width: max-content !important;
    
  top: 10;
  
  left: 80%;
 
  transform: translateX(-50%);
}
/*common styling*/
.page-title{
    font-size: 25px;
    color: rgb(22, 16, 16);
   
}
.card-title{
    font-size: 1rem;
    font-weight: bold;
}
.normal-text{
    font-size: 1rem;
}

.card{
   box-shadow: 0 0 2px gray;
   border-radius: 0 !important;
}
.spinner-parent{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.244);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
}

.anchor{
    color: #2462e8;
    text-decoration: underline !important;
    cursor: pointer;
}
.approval{
    color:blue;
    text-decoration: underline;
    font-size: 20px;
    cursor: pointer;
}
.header-action-icon{
  font-size: 30px ;
  color: black !important; 
  cursor: pointer;
  padding-left: 10px; 
}
.ant-picker-input-input{
    margin-bottom: 10px 0 !important;
}
.ant-picker{
    height: 40px !important;
}
/*authentication pages*/
.authentication{
    height: 100vh;
    display:flex;
    align-items: center;
    justify-content: center;
}
.authentication-form{
    width: 400px;
}
.authentication .card-title{
    font-size: 1.5rem;
    font-weight: bold;
    background-color: brown;
    color: white !important;
    max-width: max-content !important;
    padding: 7px 15px;
    margin-left: -40px;
    border-bottom-left-radius: 10px;

}